import React from 'react'
import { connect } from 'react-redux'
import { fetchUserData } from '../store'

const TestRemove = ({ data, stateData }) => {
    return (
        <div>
            {data}
            <h1>{stateData.url}</h1>
        </div>
    )
}

TestRemove.getInitialProps = ({ reduxStore }) => {
    reduxStore.dispatch(fetchUserData())
    return {
        data: "random"
    }
}

const mapStateToProps = state => {
    return {
        stateData: state.userData
    }
}

export default connect(mapStateToProps)(TestRemove)